body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-colour-1: rgb(240, 244, 255);
  --bg-colour-2: rgb(204, 223, 252);
  --bg-colour-3: rgb(159, 183, 219);
  --switch-colour: rgb(159, 183, 219);
}

[colour-theme="dark"] {
  --bg-colour-1: rgb(24, 26, 33);
  --bg-colour-2: rgb(55, 64, 74);
  --bg-colour-3: rgb(88, 107, 128);
  --switch-colour: rgb(45, 193, 136);
}
